import transcriper from '../modules/transcriper';
import randomColor from 'randomcolor';
import { updateTranscript, calcSpeechRate } from '../modules/transcriptManager';
import { broadcastSpeechFrame } from '../modules/backendService';


const initialState = {
  segments: [],
  server: 'NEDERLANDS',
  transcriper,
  speechStartTime: 0,
  transcriping: false,
  speakers: {},
  speechRate: 0,
};

const transcriptReducer = (state = initialState, action) => {
  switch(action.type) {
    case 'SERVER_CHANGE':
      state.transcriper.changeServer(action.url);
      return {...state, server: action.server}
    case 'CLEAR_TRANSCRIPT':
      return { ...state, segments: [] };
    case 'SPEECH_START':
      state.transcriper.open();
      return {...state, transcriping: true, speechStartTime: action.timestamp} ;
    case 'SPEECH_STOP':
      state.transcriper.close();
      return {...state, transcriping: false};
    case 'SPEECH_FRAME':
      const speechRate = calcSpeechRate(action.segment, state.speechRate);
      const segments = updateTranscript(state.segments.slice(), state.speakers, action.segment);
      return {...state, segments, speechRate};
    case 'ADD_SPEAKER':
      return {...state, speakers: addSpeaker({...state.speakers}, action)};
    case 'TRANSCRIPTION_DONE':
      broadcast(state.speakers, action.data);
      return state;
    default:
      return state;
  }
}

const addSpeaker = (speakers, action) => {
  if (!speakers[action.id]) {
    const name = `Speaker ${Object.keys(speakers).length+1}`;

    speakers[action.id] = {
      name,
      color: randomColor({luminosity: 'light',
      }),
      peerConnection: action.peerConnection,
    };
  }
  return speakers;
}

const broadcast = (speakers, data) => {
  broadcastSpeechFrame(data);
}

export default transcriptReducer;



