import React from 'react';
import Rightbar from 'components/ui/Rightbar';
import { translate } from 'react-i18next';
import Chip from 'material-ui/Chip';
import 'components/usecases/suggestions.css';
import {List, ListItem} from 'material-ui/List';
import FontIcon from 'material-ui/FontIcon';

class SuggestionsPane extends React.Component {
  render = () => {
    const props = this.props;

    return <Rightbar
              title={props.t("suggestions_title")}
              desc={props.t("suggestions_description")} >
              <div className="Suggestions__context-container">
                {this.renderContext(props.context)}
              </div>
              <List className="Suggestions__list">
                {props.content.map(this.renderSuggestion)}
              </List>
           </Rightbar>
  }

  renderSuggestion = (suggestion, idx) => {
    return(
      <ListItem className="Suggestions__listItem" key={idx}>
        <FontIcon
          className="Suggestions__listMarker fa fa-long-arrow-right">
        </FontIcon>
        <label>
          {suggestion.question}
          <p className="Suggestions__answer">{suggestion.answer}</p>
        </label>
      </ListItem>
    );
  }

  renderContext = context => {
    if (context) {
      return(
        <Chip className="Suggestions__context">
          {context}
        </Chip>
      );
    } else 
      return <span />;
  }
}

export default translate('translations')(SuggestionsPane);



