import { connect } from 'react-redux';
import Transcript from '../../components/transcript';
import { clearTranscript, addSpeaker } from '../../actions/transcript';
import { getSegments } from '../../selectors/transcript';
import identity from '../../modules/identity';


const mapStateToProps = state => {
  return {
    segments: getSegments(state),
  };
}

const mapDispatchToProps = dispatch => {
  return {
    onTranscriptClear: () => dispatch(clearTranscript()),
    generateId: () => dispatch(addSpeaker(identity)),
  };
}

const TranscriptContainer = connect(
  mapStateToProps,
  mapDispatchToProps,
)(Transcript);

export default TranscriptContainer;



