const initialState = {
  context: null,
  content: [],
}

const suggestionsReducer = (state = initialState, action) => {
  switch(action.type) {
    case 'SUGGESTIONS_LOADED':
      if (!action.context)
        return state;
      return {...state, context: action.context, content: action.suggestions};
    default:
      return state;
  }
}

export default suggestionsReducer;



