import { get } from '../modules/backendService';

const suggestionsLoaded = (context, suggestions) => {
  return {
  type: 'SUGGESTIONS_LOADED',
  context,
  suggestions,
  }
};

export const loadSuggestions = context => {
  context = encodeURIComponent(context);
  return dispatch => get(`/suggestions/check?context=${context}`)
    .then(response => dispatch(suggestionsLoaded(response.data.context, response.data.suggestions)))
    .catch(err => console.log(err));
}




